}
.galleryMain{
    margin-top: 100px;
    background-color: #EC2229;
    text-align: center !important;
    padding: 30px 30px 50px 30px;
}
.galleryHeading{
    font-family: 'Noto Serif', serif;
    font-weight: 700 !important;
    text-transform: uppercase;
    color: white;
    margin-bottom: 30px;
}
.clienteleMain{
    margin: auto;
    text-align: center;
}
.clienteleLine{
    font-size: 20px;
}
.flagMargin{
    margin-top: 50px;
}
.flagsMain{
    width: 90%;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
.flagImg{
    margin: 5px;
    width: 100%;
    height: 150px;
    box-shadow: 5px 5px 20px #d4d4d491;
    object-fit: fill;
}
.aboutMain{
    width: 100%;
    margin-top: 100px;
    background-color: #ececec;
}
.aboutBox{
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.aboutText{
    width: 60%;
    margin-right: 30px;
    padding: 20px;
}
.aboutImg{
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.aboutTextHeading{
    font-family: 'Noto Serif', serif;
    color: #EC2229;
    font-weight: 600;
}
.aboutTextPara{
    font-size: 15px !important;
    color: black;
}





@media only screen and (max-width: 460px) {
    .logo{
        width: 60px;
        margin-right: 5px;
    }
    .companyName{
        font-size: 16px !important;
    }
    .carousel{
        position: absolute;
        top: 40px;
    }
    .welcomeHeading{
        font-size: 20px !important;
    }
    .brandsHeadingMain{
        margin-top: 30px !important;
    }
    .brands{
        width: 100px !important;
        height: 100px !important;
        margin: 10px 2px 10px 2px;
    }
    .brandSKFFheading{
        font-size: 8px !important;
    }
    .quoteMain{
        margin-top: 30px !important;
    }
    .quote{
        font-size: 18px !important;
    }
    .introLine{
        font-size: 15px !important;
        margin: 20px 10px 20px 10px;
    }
    .productionImg{
        margin-top: 30px !important;
    }
    .aboutMain{
        margin-top: 30px !important;
    }
    .aboutBox{
        flex-direction: column !important;
    }
    .aboutImg{
        width: 80% !important;
        margin: 10px !important;
    }
    .aboutText{
        width: 100% !important;
        margin-right: 10px !important;
        padding: 5px !important;
    }
  }

.galleryImgContainer{
    object-fit: fill !important;
    height: 300px;
}
.sliderImg{
    width: 100%;
    transition-duration: 1s;
}
.galleryImgContainer :hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
}


@media only screen and (max-width: 1000px) {
